import React from "react";
import styled from "styled-components";
import Button from "./Button";
import triangleShape from "./images/bg-triangle.svg";
import symbols from "./rules";

const Container = styled.div`
  margin: 100px 7rem 20px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  @media only screen and (max-width: 700px) {
    transform: scale(0.7);
    margin: 30px auto 40px auto;
  }
  @media only screen and (max-width: 400px) {
    transform: scale(0.65);
  }
`;

const Triangle = styled.div`  
  background-image: url("${triangleShape}");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  min-width: 400px;
  min-height: 400px;
  position: relative;
  `;

function ButtonsPanel(props) {
  return (
    <Container>
      <Triangle>
        <Button symbol={symbols.scissors} makeChoice={props.makeChoice} />
        <Button symbol={symbols.paper} makeChoice={props.makeChoice} />
        <Button symbol={symbols.rock} makeChoice={props.makeChoice} />
      </Triangle>
    </Container>
  );
}

export default ButtonsPanel;